import {getPayDetail,pay} from '../api'
import moment from 'dayjs'
var utc = require('dayjs/plugin/utc')
moment.extend(utc)


export let PayMixin = {
    data(){
        return {
            id:'',
            tk:'',
            date:moment().utc().format(),
            orderDetail:null,
            payMode:null,
            sRealName:'',
            overtime:'',
            isOverTime:null,
            iCurrentTime:0,
            iExpireTime:0,
            getOrderDetailInterval:null,
        }
    },
    computed:{
        initPayMode(){
            let payMode = ''
            if(this.payMode){
                switch (this.payMode.iPayDictId){
                    case 13:
                        payMode = ''
                        break
                    case 14:
                        payMode = ''
                        break
                    case 15:
                        payMode = ''
                        break
                }
            }
            return payMode
        },
        orderStateCode(){
            return this.orderDetail ? this.orderDetail.iOrderState : null
        },
        status(){
            let status = ''
            if(this.orderDetail){
                switch (this.orderDetail.iOrderState){
                    case 1:
                        status = 'info'
                        break
                    case 2:
                        status = 'success'
                        break
                    case 3:
                        status = 'warning'
                        break
                    case 4:
                        status = 'success'
                        break
                    case 10:
                        status = 'success'
                        break
                    case 11:
                        status = 'error'
                        break
                    case 12:
                        status = 'error'
                        break
                }
            }
            return status
        },
        orderState(){
            let orderState = ''
            if(this.orderDetail){
                switch (this.orderDetail.iOrderState){
                    case 1:
                        orderState = ''
                        break
                    case 2:
                        orderState = ''
                        break
                    case 3:
                        orderState = ''
                        break
                    case 4:
                        orderState = ''       //
                        break
                    case 10:
                        orderState = ''
                        break
                    case 11:
                        orderState = ''
                        break
                    case 12:
                        orderState = ''
                        break
                }
            }
            return orderState
        }
    },
    created(){
        this.id = this.$route.query.id
        this.tk = this.$route.query.tk
        this.getOrderDetail()
        this.getOrderDetailInterval = setInterval(()=>{
            this.getOrderDetail()
        },3000)

    },
    beforeDestroy(){
      clearInterval(this.getOrderDetailInterval)
    },
    methods:{
        confirmPayment(){
            if(this.sRealName ===''){
                this.$message.error('')
                return
            }
            this.$confirm({
                title:'',
                content:'',
                okText:'',
                cancelText:'',
                onOk:()=>{
                    const params = {
                        sRealName:this.sRealName,
                        sOrderId:this.id
                    }
                    const headers = {
                        'ACCESS-TIMESTAMP':moment().utc().format(),
                        'ACCESS-SIGN':this.tk
                    }
                    pay(params,headers)
                        .then(res=>{
                            if(res.code === 0){
                                getPayDetail(params,headers)
                                    .then(res=>{
                                        // debugger
                                        this.orderDetail = res.data.stOrderData
                                        this.iCurrentTime = res.data.iCurrentTime
                                        this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                                    })
                            }
                        })
                }
            })
        },
        overTimeInterval(){
            if(this.iExpireTime <= 0){
                this.isOverTime = true
                const params = {
                    sOrderId:this.id
                }
                const headers = {
                    'ACCESS-TIMESTAMP':moment().utc().format(),
                    'ACCESS-SIGN':this.tk
                }
                getPayDetail(params,headers)
                    .then(res=>{
                        // debugger
                        this.orderDetail = res.data.stOrderData
                        this.iCurrentTime = res.data.iCurrentTime
                        this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    })
                return
            }
            // debugger
            let m = parseInt(this.iExpireTime/60000).toString()
            let s = (parseInt((this.iExpireTime%60000)/1000)).toString()
            if(m.length === 1)m ='0' + m
            if(s.length === 1)s ='0' + s
            this.overtime =  m + ' : ' + s
            this.iExpireTime -=1000
            setTimeout(()=>this.overTimeInterval(),1000)
        },
        getOrderDetail(){
            const params = {
                sOrderId:this.id
            }
            const headers = {
                'ACCESS-TIMESTAMP':moment().utc().format(),
                'ACCESS-SIGN':this.tk
            }
            getPayDetail(params,headers)
                .then(res=>{
                    if(res.code === 0){
                        this.orderDetail = res.data.stOrderData
                        this.iCurrentTime = res.data.iCurrentTime
                        this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                        this.payMode = res.data.vUsedList[0]
                        this.overTimeInterval()
                    }
                    console.log(res)
                })
        }
    }
}
