                  <a-button v-if="orderDetail && orderStateCode ===1" size="small" v-clipboard:copy="orderDetail.dTradeAmount" v-clipboard:success="()=>$message.success('')" v-clipboard:error="()=>$message.error('')" style="margin-left: 16px"></a-button>
                </a-col>
              </a-row>
<!--              <a-row :gutter="16">-->
<!--                <a-col :span="8" class="row-title">:</a-col>-->
<!--                <a-col :span="16"  class="row-content">{{initPayMode}}</a-col>-->
<!--              </a-row>-->
<!--              <template v-if="payMode && payMode.iPayDictId ===15">-->
              <template>
                <a-row :gutter="16" >
