<script>
import {PayMixin} from "./payMixin";
    import {Button,Input,Card,Result,Row,Col,Icon} from 'ant-design-vue'

export default {
name: "web",
  mixins:[PayMixin],
  components:{
    [Button.name]:Button,
    [Input.name]:Input,
    [Card.name]:Card,
    [Result.name]:Result,
    [Row.name]:Row,
    [Col.name]:Col,
    [Icon.name]:Icon,
  },

  created(){
    document.documentElement.style.fontSize = "80px"
  }
}
</script>

<style lang="less" scoped>
  .desc{

  }
  .row-title{
    text-align: right;
    font-size: 0.2rem;
    line-height: 0.5rem;
    font-weight: bold;
  }
  .row-content{
    font-size: 0.2rem;
    line-height: 0.5rem;
  }
  .amount{
    font-size: 0.35rem;
    color:red
  }
</style>